import axios from "axios";
import { createStore } from "vuex";
import as_data from "./assets/Data";

const store = createStore({
  state() {
    return {
      name: "kim",
      age: 20,
      likes: 0,

      st_data: as_data,
      more: {} /*서버에서 더보기 게시물 가져와서 여기에 저장*/,
    };
  },

  actions: {
    getData(context) {
      axios.get("https://codingapple1.github.io/vue/more0.json").then((a) => {
        console.log(a.data);
        context.commit('setMore', a.data)
      });
    },
  },

  mutations: {
    setMore(state, data) {
      state.more = data;
    },
    좋아요(state, click_id) {
      if (state.st_data[click_id].clickYn == 0) {
        state.st_data[click_id].clickYn = 1;
        state.st_data[click_id].likes++;
      } else {
        state.st_data[click_id].clickYn = 0;
        state.st_data[click_id].likes--;
      }
    },
  },
});

export default store;
