<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step != 0" @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish()">추가</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <!-- <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">더보기버튼</button> -->

  <Container
    :R_data="R_data"
    :step="step"
    :addImg="addImg"
    @write1="작성한글 = $event"
  ></Container>

  <!-- <button @click="more">더보기</button> -->

  <div v-if="step == 0" class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">
        <img src="./assets/플러스버튼.png"
      /></label>
    </ul>
  </div>
</template>

<script>
import Container from "./components/Container.vue";
import axios from "axios";
import { mapMutations, mapState } from 'vuex';

export default {
  name: "App",
  data() {
    return {
      R_data: this.$store.state.st_data,
      btCnt: 0,
      step: 0,
      addImg: null,
      작성한글: "",
      emi_filterNm: "",
    };
  },
  components: {
    Container: Container,
  },

  computed: {
    /* store의 변수를 미리 셋팅해서 사용 */
    name() {
      return this.$store.state.name;
    },

    ...mapState(['name', 'age', 'likes']),
    ...mapState({이름:'name', 나이:'age'}), /* 변수명 바꾸고 싶을경우 */
  },

  methods: {
    ...mapMutations(['setMore', '좋아요']), /* store의 매소드를 사용할 경우*/

    more() {
      if (this.btCnt == 0) {
        axios
          .get("https://codingapple1.github.io/vue/more0.json")
          .then((aa) => {
            console.log(aa.data);
            this.R_data.push(aa.data);
            this.btCnt++;
          });
      } else {
        axios
          .get("https://codingapple1.github.io/vue/more1.json")
          .then((aa) => {
            console.log(aa.data);
            this.R_data.push(aa.data);
            this.btCnt++;
          });
      }
    },

    upload(e) {
      let 파일 = e.target.files;
      console.log(파일[0]);
      let url = URL.createObjectURL(파일[0]);
      console.log(url);
      this.addImg = url;
      this.step++;
    },

    publish() {
      var 내게시물 = {
        name: "지렁이 is 꿈틀",
        userImage: "https://placeimg.com/100/100/nature/sepia",
        postImage: this.addImg,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: this.emi_filterNm,
        clickYn: 0,
      };

      this.R_data.unshift(내게시물);
      this.step = 0;
    },
  },

  mounted() {
    this.emitter.on("필터", (a) => {
      this.emi_filterNm = a;
    });
  },
};
</script>

