<template>
  <div
    @click="fire"
    :class="filterNm + ' filter-item'"
    :style="`background-image: url(${addImg}) `"
  >
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "FilterBox_a",

  data() {
    return {
      msg: "hello",
    };
  },

  props: {
    addImg: String,
    filterNm: String,
  },

  methods: {
    fire() {
      this.emitter.emit("필터", this.filterNm);
    },
  },
};
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: black;
  background-size: cover;
  background-position: center;
}
</style>
