<template>
  <div>
    <div v-if="step == 0">
      <Post :R_data="R_data[i]" :key_id="i" v-for="(a, i) in R_data" :key="i" />
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div
        :class="emi_filterNm"
        class="upload-image"
        :style="`background-Image: url(${addImg})`"
      ></div>
      <div class="filters">
        <FilterBox
          :filterNm="filterNm"
          :addImg="addImg"
          v-for="filterNm in 필터들"
          :key="filterNm"
        >
          <!-- <template v-slot:default="작명"> <span>{{작명.msg + ' 부모에서 추가 '}} </span> </template> -->
          <span>{{ filterNm }}</span>
        </FilterBox>
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div
        :class="emi_filterNm"
        class="upload-image"
        :style="`background-Image: url(${addImg})`"
      ></div>
      <div class="write">
        <li class="note">내용</li>
        <textarea
          @input="$emit('write1', $event.target.value)"
          class="write-box"
        ></textarea>
      </div>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";

export default {
  name: "Container_a",

  components: { Post, FilterBox },

  data() {
    return {
      필터들: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],

      emi_filterNm: "",
    };
  },

  props: {
    R_data: Array,
    step: Number,
    addImg: String,
  },

  mounted() {
    this.emitter.on("필터", (a) => {
      this.emi_filterNm = a;
    });
  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  padding-top: 10px;
  border: 10;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
.note {
  padding-top: 10px;
}
</style>
